body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.bg {
min-width: 100vw;
min-height: 100vh;
background-image: url('http://i.imgur.com/w16HASj.png');
background-size: cover;
}

.App {
  min-height: 100vh;
}

.content {
  margin-top: 20%
}

.centered {
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%)
}

.InputStyle {
  border-radius: 25px;
  border: solid 1px white;
  background: transparent;
  width: 300px;
  padding: 10px 20px
}

input,
input::-webkit-input-placeholder {
  font-size: 12px;
}

.social-btn {
  position: absolute;
  bottom: 20px;
  left: 47%
}

i {
  padding: 5px;
  color: white
}

input,
input:focus {
  outline: 0;
  -webkit-appearance: none;
  box-shadow: nones;
  -moz-box-shadow: none;
  -webkit-box-shadow: none
}

button {
  order: solid 1px white;
  outline: 0;
}

.secondLine {
  font-weight: 350;
  font-size: 15px;
  margin-bottom: 15%;
  color: white
}

.firstLine {
  font-size: 30px;
  color: white
}

@media only screen and (max-width: 600px) {
  .firstLine {
      font-size: 20px
  }
}

